*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container{
    width: 100%;
    height: 100vh;
    background:floralwhite;
    display: flex;
    align-items: center;
    justify-content: center;
}

.box{
    width: 350px;
    height: 400px;
    border: 2px solid black;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    border-radius: 5px;
}

.input-data{
    width: 300px;
    height: 60px;
    margin-top: 25px;
}
.input-data input{
    width: 100%;
    height: 100%;
    border-radius: 5px;
}

.info{
    background-color: #e0ecec;
    margin-top: 50px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
    
}

.location{
    font-size: 25px;
    font-weight: 500;
}

.temp-minmax{
    font-weight: 400;
}

